Vue.use(Router)

var router = new Router({
  // mode: 'history',
  // base: '/product-library.html',
  routes: [
//		{
//    path: '/',
//    redirect: '/all-picture'
//  },
    {
      path: '/',
      name: 'product-library',
      component: ProductLibrary
    }
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


